
/* Marker Styles */

img.first-marker1 {
  width: 45px;
  height:45px; 
}

 img.first-marker {
  width: 45px;
  height:45px; 
}

img.first-marker:hover{
  width: 55px;
  height: 55px;
}


.red-dot {
  background-color: red;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  cursor: pointer;
}

.red-dot:hover{
  width:21px;
  height:21px;
}


.marker-container {
  position: relative; 
  display: flex; 
  flex-direction: column;
  align-items: center;
}

.marker-btn {
  background: none;
  border: none;
  cursor: pointer;
  width: 100;
  height: 100px;
}

.marker-icon {
  width: 30px; 
  height: 30px; 
}

.marker-dot {
  width: 15px; 
  height: 15px;
  background-color: red; 
  border-radius: 50%;
  margin-top: 4px; 
}

.weather-icon {
  position: absolute;
  top: -30px; 
  left: 200%;
  transform: translateX(-50%) scale(1.5); 
  width: 30px;
  height: 30px; 
  pointer-events: none; 
}

/* Sidebar */
.sidebar {
  width: 400px; 
  padding: 20px;
  background-color: #f8f9fa;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  flex-shrink: 0;
  overflow-y: auto; 
}

.map-area {
  flex: 1;
  position: relative;
}

/* Calculate Button */
.calculate-button {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  background-color: #1E90FF;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}

.calculate-button:disabled {
  background-color: #6c757d; 
  cursor: not-allowed;
}

/* Clear Button */
.clear-button {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  cursor: pointer;
  background-color: #dc3545; 
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}

.clear-button:disabled {
  background-color: #6c757d; 
  cursor: not-allowed;
}

/* Route List */

.route-list li {
  margin-bottom: 10px;
  align-items: center;
}

.route-list img {
  width: 20px;
  height: 20px;
  margin-left: 10px;
}

/* Clear Message */
.clear-message {
  margin-top: 10px;
  padding: 10px;
  background-color: #d4edda; 
  color: #155724;
  border: 1px solid #c3e6cb;
  border-radius: 4px;
}


/* Distance Checkbox*/
.distance-checkbox {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.distance-checkbox input {
  margin-right: 10px;
  transform: scale(1.2); 
}

.distance-checkbox label {
  font-size: 16px;
}